.navegacao{
    background-color: rgb(55, 21, 61);
    display:grid;
    grid-template-columns: 30% 70%;
    padding-left: 1%;
    padding-right: 1%;
    padding-top: 1%;
    color: rgb(132, 9, 247);
}

.navbar{
    display:grid;
    grid-template-columns: 20% 20% 20% 20%;
    list-style: none;
    padding-top: 2%;
    background-color: rgb(55, 21, 61);
    align-items: center;
    justify-content: center;
    justify-items: center;
}

li, a{
    color: rgb(132, 9, 247);
width: auto;
margin-right: -60%;
min-width: 80%;
text-align: center;
justify-content: center;
justify-items: center;
font-size: large;

}

li:hover{
    color: white;

}
a:hover{
    color: white;

}

a{
    text-decoration: none;
}

a:visited, li:visited{
    display: none;
}



@media only screen and (max-width:480px){
    .navegacao{
      
    width: 350;
    height: 100px;
    }
    
}

@media only screen and (max-width:480px){
    .navbar{
 
   width: 340px;
   text-align: center;
   align-items: center;
   justify-items: center;
   text-align: left;
   justify-content: left;
    height: auto;
    padding: 0;
    margin: 0;
    }
    
}

@media only screen and (max-width:480px){
   h1{
   display: none;
    }
    
}