#sobre{
    display: flex;
    color: rgb(7, 7, 7);
    background-color: rgb(83, 26, 94);
    height: 600px;
    
    text-align: left;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    
}


.article{
    width: 450px;
    padding: 15px;
    background: rgb(9, 185, 177);
    position: absolute;
    border: 1px;
    border-radius: 20px;
    text-align: left;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    color: rgb(7, 7, 7);
}

h3{
    color: rgb(255, 255, 255);
}


@media only screen and (max-width:480px){
    .article {
    margin-left: 15px;
      width: 300px;
    
    }
    
}

@media only screen and (max-width:480px){
    #sobre {
  
        width: 360px;
      

   
    }
   
}