#main{
  
    display:grid;
    grid-template-columns: 45% 45%;
  
    padding-left: 1%;
    padding-right: 1%;
    padding-top: 10%;
    gap: 10%;
   height: 500px;
   padding-bottom: 5%;
   padding-top: 0px;
   
}

#left{
 
display: grid;
grid-template-columns:  1fr;
align-items: center;
justify-content: center;
justify-items: left;


}




#Right{
    position: relative;
   display: grid;
   grid-template-columns:30% 30% ;
   grid-template-rows:30% 30% ;
   padding-top: 50px;
   align-items: center;
   justify-content: center;
   justify-items: center;
   gap: 1px;
   
 
}






@media only screen and (max-width:480px){
    #main {
   
    height: 400px;
    display: flex;
    flex-direction: column;

  margin: 0;
  padding: 0;
  
    
    }
    
}


@media only screen and (max-width:480px){
    #Right {
        align-items: center;
        justify-content: center;
        justify-items: center;
 display: flex;

 height: 200px;
 padding-top: 0;
 margin-top: -30px;
 width: 360px;
 
    }
    
}

@media only screen and (max-width:480px){
    #left {    
 display: flex;
 height: 200px;
 margin-top: 3%;
 padding-top: 0;

 width: 360px;
    }
    
}


