.Conteudoleft{
    display: flex;
    flex-direction: column;
    margin: 0;
    align-items: flex-start;
    
    }

    

    
#left  span{
    
    font-size: medium;
    color: rgb(180, 177, 180);
}

#left  h2{
   
    font-size: 35px;
    margin-top: 10px;
    margin-bottom: 1px;
    color: rgb(180, 177, 180);
}

#left  h5{
    
    width: 250px;
    font-size: 20px;
    margin-top: 0px;
    color: rgb(180, 177, 180);
    margin-bottom: 0px;
}
#left  p{
   
    width: 250px;
    font-size: 15px;
    margin-top: 0px;
    color: rgb(180, 177, 180);
}


#left img{
    
    margin-top: 0px;
   width: 180px;
   height: 180px;
   border-radius: 50%;
   margin-left: 75px;
   margin-bottom: -40px;
 transform: translateY(0px);
 
}



@keyframes animatep{
    0% { 
    transform: translate(-300px);
    }
    
    50% { transform: translate(-10px);
    }
    100% { transform: translate(0px);
    }
    }

    #left img{
        content:'' ;
       
       animation: animatep 5s;
        position: relative;
        inset: 1px;
    }
        



    @media only screen and (max-width:480px){
        .Conteudoleft {
        width: 350px;
           text-align: left;
        
        }
        
    }
    @media only screen and (max-width:480px){
        #left  h2{
        width: 350px;
           text-align: left;
        
        }
        
    }
    
    @media only screen and (max-width:480px){
        #left img {
        
           display: none;
        
        }
        
    }