#projetos{
    display: grid;
  align-items: center;
  justify-content: center;
  justify-items: center;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-columns: 100% ;
    background-color: rgb(83, 26, 94);
    height: 800px;
    color: rgb(252, 243, 252);
    font-family: Arial, Helvetica, sans-serif;
    
}




#projetos h2{
   text-align: center;
   
}

#projetos div{
    color: rgb(3, 3, 3);
   
 background-color: rgb(21, 233, 233);
  width: 460px;
  height: 80%;
 margin-top: 3px;
 margin-bottom: 5px;
 border-radius: 5px;
 padding: 5px;
 
 
    }


    @media only screen and (max-width:480px){
    #projetos{
     
        width: 360px;
        margin-left: 8px;

      
        }
        
    }
    
    @media only screen and (max-width:480px){
        #projetos div, h2{
          align-items: center;
            margin-left: 0;
            width: 300px;
            justify-items: center;
            text-align: center;
           
            }
            
        }
        