
section{
   
    width: 100px;
    height: 100px;
    background-color:white;
    text-align: center;
    border-radius: 10%;
    transform: translateY(0px);
    
    
    }
    
    
    
    .img{
        width:50px;
        height: 50px;
        margin-top: 25%;
       border-radius: 1%;
       color: rgb(248, 248, 248) ;
       
    }
    
    .img:hover{
        width:60px;
        height: 60px;
        margin-top: 20%;
       border-radius: 1%;
     
       
    }
    




 section:hover{
    border-radius: 10px;
    width: 100px;
    height: 100px;

}



@keyframes animate{
    0% { 
    transform: translate(0px);
    }
    
    50% { transform: translate(50px);
    }
    100% { transform: translate(0px);
    }
    }

    section{
        content:'' ;
       
       animation: animate 5s linear infinite ;
        position: relative;
      
    }



    @media only screen and (max-width:480px){
        section{
            width: 60px;
            height: 60px;
            background-color:white;
            border-radius: 50%;
           animation: none;
           
          
        }
        
    }


    @media only screen and (max-width:480px){
        .img{
       width:35px;
        height: 35px;
        margin-bottom: 5%;
       border-radius: 1%;
       color: rgb(248, 248, 248) ;
        
        }
        
    }

   
    @media only screen and (max-width:820px){
        section{
            width: 60px;
